<template>
  <div class="calculator-wrap">
    <el-backtop>
      <div style="
           {
            height: 100%;
            width: 100%;
            background-color: black;
            box-shadow: 0 25.6px 57.6px 0 rgba(0, 0, 0, 0.22), 0 4.8px 14.4px 0 rgba(0, 0, 0, 0.18);
            text-align: center;
            line-height: 40px;
            color: white;
            border: solid 1px white;
          }
        ">
        Top
      </div>
    </el-backtop>
    <Header></Header>
    <DataSrcDeclare></DataSrcDeclare>
    <div class="calculator-pane">
      <div class="input-area">
        <el-form :model="formData"
                 ref="formData"
                 :rules="formRules">
          <el-form-item>
            <el-select style="width: 100%;"
                       placeholder=""
                       clearable
                       v-model="formData.weaponId"
                       @change="handleWeaponChange">
              <el-option v-for="item in weaponSelectOpts"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item v-if="formData.weaponId === '11'">
            <div class="item-row">
              <div class="row">
                <el-radio-group v-model="formData.bottleType">
                  <el-radio :label="0"></el-radio>
                  <el-radio :label="1"></el-radio>
                  <el-radio :label="2"></el-radio>
                </el-radio-group>
              </div>
            </div>
          </el-form-item>
          <el-form-item v-if="formData.weaponId === '12' || formData.weaponId === '13'">
            <div class="item-row">
              <div class="row">
                <span></span>
                <el-input-number v-model="formData.closeItemNumber"
                                 :step="1"
                                 :min="0"
                                 :precision="0"
                                 :max="4"
                                 size="small"
                                 placeholder="0"></el-input-number>
              </div>
              <div class="row">
                <span></span>
                <el-input-number v-model="formData.farItemNumber"
                                 :step="1"
                                 :min="0"
                                 :precision="0"
                                 :max="4"
                                 size="small"
                                 placeholder="0"></el-input-number>
              </div>
              <div class="row"
                   v-if="formData.weaponId === '12'">
                <el-select style="width: 100%;"
                           placeholder=""
                           v-model="formData.isScope">
                  <el-option v-for="item in isScopeOpts"
                             :key="item.value"
                             :label="item.label"
                             :value="item.value"></el-option>
                </el-select>
              </div>
            </div>
          </el-form-item>
          <el-form-item>
            <el-input v-model="formData.weaponShowAtt"
                      placeholder="()"
                      clearable></el-input>
          </el-form-item>
          <el-form-item v-if="formData.weaponId !== '12' && formData.weaponId !== '13'">
            <el-input v-model="formData.weaponShowEl"
                      placeholder="()"
                      clearable></el-input>
          </el-form-item>
          <el-form-item v-if="formData.weaponId === '12' || formData.weaponId === '13'">
            <el-select style="width: 100%;"
                       v-model="formData.weaponShowEl"
                       placeholder=""
                       @change="handleSetElement">
              <el-option v-for="item in elBulletTypes"
                         :key="item.value"
                         :value="item.value"
                         :label="item.label"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select style="width: 100%;"
                       placeholder=""
                       v-model="formData.customAttLv">
              <el-option v-for="item in customAttLvOpts"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select style="width: 100%;"
                       placeholder=""
                       v-model="formData.customElLv">
              <el-option v-for="item in customElLvOpts"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <div class="awake-area">
              <span class="title">()</span>
              <div class="row">
                <span class="text"></span>
                <el-select size="small"
                           placeholder=""
                           v-model="formData.awakeAttackLv[0]">
                  <el-option v-for="item in awakeLvOpts"
                             :key="item.value"
                             :label="item.label"
                             :value="item.value"></el-option>
                </el-select>
              </div>
              <div class="row">
                <span class="text"></span>
                <el-select size="small"
                           placeholder=""
                           v-model="formData.awakeAttackLv[1]">
                  <el-option v-for="item in awakeLvOpts"
                             :key="item.value"
                             :label="item.label"
                             :value="item.value"></el-option>
                </el-select>
              </div>
              <div class="row">
                <span class="text"></span>
                <el-select size="small"
                           placeholder=""
                           v-model="formData.awakeAttackLv[2]">
                  <el-option v-for="item in awakeLvOpts"
                             :key="item.value"
                             :label="item.label"
                             :value="item.value"></el-option>
                </el-select>
              </div>
              <div class="row">
                <span class="text"></span>
                <el-select size="small"
                           placeholder=""
                           v-model="formData.awakeAttackLv[3]">
                  <el-option v-for="item in awakeLvOpts"
                             :key="item.value"
                             :label="item.label"
                             :value="item.value"></el-option>
                </el-select>
              </div>
              <div class="row">
                <span class="text"></span>
                <el-select size="small"
                           placeholder=""
                           v-model="formData.awakeAttackLv[4]">
                  <el-option v-for="item in awakeLvOpts"
                             :key="item.value"
                             :label="item.label"
                             :value="item.value"></el-option>
                </el-select>
              </div>
            </div>
          </el-form-item>
          <el-form-item>
            <div class="awake-area">
              <span class="title">()</span>
              <div class="row">
                <span class="text"></span>
                <el-select size="small"
                           placeholder=""
                           v-model="formData.awakeElementLv[0]">
                  <el-option v-for="item in awakeElLvOpts"
                             :key="item.value"
                             :label="item.label"
                             :value="item.value"></el-option>
                </el-select>
              </div>
              <div class="row">
                <span class="text"></span>
                <el-select size="small"
                           placeholder=""
                           v-model="formData.awakeElementLv[1]">
                  <el-option v-for="item in awakeElLvOpts"
                             :key="item.value"
                             :label="item.label"
                             :value="item.value"></el-option>
                </el-select>
              </div>
              <div class="row">
                <span class="text"></span>
                <el-select size="small"
                           placeholder=""
                           v-model="formData.awakeElementLv[2]">
                  <el-option v-for="item in awakeElLvOpts"
                             :key="item.value"
                             :label="item.label"
                             :value="item.value"></el-option>
                </el-select>
              </div>
              <div class="row">
                <span class="text"></span>
                <el-select size="small"
                           placeholder=""
                           v-model="formData.awakeElementLv[3]">
                  <el-option v-for="item in awakeElLvOpts"
                             :key="item.value"
                             :label="item.label"
                             :value="item.value"></el-option>
                </el-select>
              </div>
              <div class="row">
                <span class="text"></span>
                <el-select size="small"
                           placeholder=""
                           v-model="formData.awakeElementLv[4]">
                  <el-option v-for="item in awakeElLvOpts"
                             :key="item.value"
                             :label="item.label"
                             :value="item.value"></el-option>
                </el-select>
              </div>
            </div>
          </el-form-item>
          <el-form-item>
            <div class="base-skills-row">
              <span class="title"></span>
              <el-checkbox-group v-model="formData.baseAttSkillList">
                <el-checkbox v-for="item in baseSkillOpts"
                             :label="item.id"
                             :key="item.id">{{ item.name }}</el-checkbox>
              </el-checkbox-group>
            </div>
          </el-form-item>
          <el-form-item>
            <div class="base-skills-row">
              <span class="title"></span>
              <el-checkbox-group v-model="formData.otherAttSkillList">
                <el-checkbox v-for="item in otherSkillOpts"
                             :label="item.id"
                             :key="item.id">{{ item.name }}</el-checkbox>
              </el-checkbox-group>
            </div>
          </el-form-item>
          <el-form-item>
            <div class="base-skills-row">
              <span class="title"></span>
              <el-checkbox-group v-model="formData.elSkillList">
                <el-checkbox v-for="item in elSkillOpts"
                             :label="item.id"
                             :key="item.id">{{ item.name }}</el-checkbox>
              </el-checkbox-group>
            </div>
          </el-form-item>
          <el-form-item>
            <div class="base-skills-row">
              <span class="title"></span>
              <el-checkbox-group v-model="formData.attLimitAfterRate">
                <el-checkbox v-for="item in attLmtAfterRateOpts"
                             :label="item.id"
                             :key="item.id">{{ item.name }}</el-checkbox>
              </el-checkbox-group>
            </div>
          </el-form-item>
          <el-form-item>
            <el-select style="width: 100%;"
                       v-model="formData.criticalSituation"
                       placeholder="">
              <el-option v-for="item in criticalOpts"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item v-if="formData.weaponId !== ''">
            <el-select style="width: 100%;"
                       v-model="formData.skillNumber"
                       placeholder="()"
                       @change="handleAction">
              <el-option v-for="(item, index) in actionList"
                         :key="index"
                         :label="item.name"
                         :value="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-input v-model="formData.action"
                      placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-input v-model="formData.elementChanger"
                      placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-input v-model="formData.meatRate"
                      placeholder="()"></el-input>
          </el-form-item>
          <el-form-item>
            <el-input v-model="formData.elMeatRate"
                      placeholder="()"></el-input>
          </el-form-item>
          <el-form-item>
            <el-input v-model="formData.angryRate"
                      placeholder="()"></el-input>
          </el-form-item>
          <el-form-item>
            <el-select style="width: 100%;"
                       v-model="formData.bladeNumber"
                       placeholder="">
              <el-option v-for="item in bladeOpts"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select style="width: 100%;"
                       v-model="formData.isBladeCheck"
                       placeholder="">
              <el-option v-for="item in isBladeCheckOpts"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <div class="result-area">
        <div class="btn-row">
          <el-button type="primary"
                     size="small"
                     @click="handleCalculator('formData')"><i class="el-icon-check"></i>&nbsp;&nbsp;</el-button>
          <div class="note"
               @click="handleGoToNote">
            <span></span>
          </div>
        </div>
        <span></span>
        <div class="row">
          <span class="pre"></span><span class="data">{{ res.totalDmg }}</span>
        </div>
        <div class="row">
          <span class="pre"></span><span class="data">{{ res.toMonsterPHYDmg }}</span>
        </div>
        <div class="row">
          <span class="pre"></span><span class="data">{{ res.toMonsterElDmg }}</span>
        </div>
        <div class="row">
          <span class="pre"></span><span class="data">{{ res.monsterPhyDmgClaw }}</span>
        </div>
        <div class="col">
          <span class="pre">()</span>
          <span class="data">{{ res.monsterPhyDmgClawSp1 }}</span>
        </div>
        <div class="col">
          <span class="pre">()</span>
          <span class="data">{{ res.monsterPhyDmgClawSp2 }}</span>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import baseLogic from '../script/Logic'
import Header from '../components/Header'
import Footer from '../components/Footer'
import DataSrcDeclare from '../components/DataSrcDeclare'
export default {
  name: 'CalculatorView',
  components: {
    Header,
    Footer,
    DataSrcDeclare
  },
  data() {
    return {
      formData: {
        weaponId: '',
        weaponShowAtt: '',
        weaponShowEl: '',
        customAttLv: '0',
        customElLv: -1,
        awakeAttackLv: [-1, -1, -1, -1, -1],
        awakeElementLv: [-1, -1, -1, -1, -1],
        limitRate: '0',
        baseAttSkillList: [],
        otherAttSkillList: [],
        elSkillList: [],
        attLimitAfterRate: [],
        closeItemNumber: 0,
        farItemNumber: 0,
        bottleType: 0,
        criticalSituation: 1,
        skillNumber: '',
        action: '',
        elementChanger: '',
        meatRate: '',
        elMeatRate: '',
        bladeNumber: 0,
        isBladeCheck: false,
        isScope: false,
        angryRate: ''
      },
      formRules: {},
      weaponSelectOpts: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: ''
        },
        {
          value: '2',
          label: ''
        },
        {
          value: '3',
          label: ''
        },
        {
          value: '4',
          label: ''
        },
        {
          value: '5',
          label: ''
        },
        {
          value: '6',
          label: ''
        },
        {
          value: '7',
          label: ''
        },
        {
          value: '8',
          label: ''
        },
        {
          value: '9',
          label: ''
        },
        {
          value: '10',
          label: ''
        },
        {
          value: '11',
          label: ''
        },
        {
          value: '12',
          label: ''
        },
        {
          value: '13',
          label: ''
        }
      ],
      customAttLvOpts: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: ''
        },
        {
          value: '2',
          label: ''
        },
        {
          value: '3',
          label: ''
        }
      ],
      customElLvOpts: [
        {
          value: -1,
          label: ''
        },
        {
          value: 0,
          label: ''
        },
        {
          value: 1,
          label: ''
        },
        {
          value: 2,
          label: ''
        },
        {
          value: 3,
          label: ''
        }
      ],
      awakeLvOpts: [
        {
          value: -1,
          label: ''
        },
        {
          value: 0,
          label: ''
        },
        {
          value: 1,
          label: ''
        },
        {
          value: 2,
          label: ''
        },
        {
          value: 3,
          label: ''
        },
        {
          value: 4,
          label: ''
        },
        {
          value: 5,
          label: ''
        }
      ],
      awakeElLvOpts: [
        {
          value: -1,
          label: ''
        },
        {
          value: 0,
          label: ''
        },
        {
          value: 1,
          label: ''
        },
        {
          value: 2,
          label: ''
        },
        {
          value: 3,
          label: ''
        },
        {
          value: 4,
          label: ''
        },
        {
          value: 5,
          label: ''
        }
      ],
      bluntLvOpts: [
        {
          value: 4,
          label: ''
        },
        {
          value: 0,
          label: ''
        },
        {
          value: 1,
          label: ''
        },
        {
          value: 2,
          label: ''
        },
        {
          value: 3,
          label: ''
        }
      ],
      baseSkillOpts: [],
      otherSkillOpts: [],
      elSkillOpts: [],
      attLmtAfterRateOpts: [],
      actionList: [],
      criticalOpts: [
        {
          value: 0,
          label: ''
        },
        {
          value: 1,
          label: ''
        },
        {
          value: 2,
          label: ''
        },
        {
          value: 3,
          label: ' Lv1'
        },
        {
          value: 4,
          label: ' Lv2'
        },
        {
          value: 5,
          label: ' Lv3'
        }
      ],
      bladeOpts: [
        {
          value: 0,
          label: ''
        },
        {
          value: 1,
          label: ''
        },
        {
          value: 2,
          label: ''
        },
        {
          value: 3,
          label: ''
        },
        {
          value: 4,
          label: ''
        },
        {
          value: 5,
          label: ''
        },
        {
          value: 6,
          label: ''
        },
        {
          value: 7,
          label: ''
        }
      ],
      elBulletTypes: [
        {
          value: '0',
          label: ''
        },
        {
          value: '22',
          label: '///'
        },
        {
          value: '18',
          label: ''
        }
      ],
      isBladeCheckOpts: [
        {
          value: false,
          label: ''
        },
        {
          value: true,
          label: ''
        }
      ],
      isScopeOpts: [
        {
          value: false,
          label: ''
        },
        {
          value: true,
          label: ''
        }
      ],
      res: {
        totalDmg: 0,
        toMonsterPHYDmg: 0,
        toMonsterElDmg: 0,
        sumAttack: 0,
        physicDmg: 0,
        elDmg: 0,
        monsterPhyDmgClaw: 0,
        monsterPhyDmgClawSp1: 0,
        monsterPhyDmgClawSp2: 0
      }
    }
  },
  mounted() {
    this.baseSkillOptsInit()
  },
  methods: {
    baseSkillOptsInit() {
      this.baseSkillOpts = JSON.parse(JSON.stringify(baseLogic.getBaseDmgRate()))
      this.otherSkillOpts = JSON.parse(JSON.stringify(baseLogic.getOtherAttack()))
      this.attLmtAfterRateOpts = JSON.parse(JSON.stringify(baseLogic.getAttLimitAfterRate()))
      this.elSkillOpts = JSON.parse(JSON.stringify(baseLogic.getElSkill()))
    },
    getActions(weaponId) {
      this.actionList.length = 0
      const WAD = () => import('../script/WeaponActionData')
      WAD()
        .then((module) => {
          return module.default
        })
        .catch((err) => {
          this.$message.error(',,errCode=' + err)
        })
        .then((m) => {
          this.actionList = m.getActionListById(weaponId)
        })
    },
    handleWeaponChange() {
      this.formData.closeItemNumber = 0
      this.formData.farItemNumber = 0
      this.formData.bottleType = 0
      this.formData.skillNumber = ''
      this.formData.action = ''
      this.formData.elementChanger = ''
      this.formData.isScope = false
      this.formData.isBladeCheck = false
      if (this.formData.weaponId !== '') this.getActions(this.formData.weaponId)
    },
    handleAction() {
      this.formData.action = this.actionList[this.formData.skillNumber].actionNum
      this.formData.elementChanger = this.actionList[this.formData.skillNumber].elementChanger
      if ((this.formData.weaponId === '12' || this.formData.weaponId === '13') && this.formData.skillNumber === 0) {
        this.formData.weaponShowEl = '22'
      }
      if ((this.formData.weaponId === '12' || this.formData.weaponId === '13') && this.formData.skillNumber === 1) {
        this.formData.weaponShowEl = '18'
      }
      if ((this.formData.weaponId === '12' || this.formData.weaponId === '13') && this.formData.skillNumber !== 0 && this.formData.skillNumber !== 1) {
        this.formData.weaponShowEl = '0'
      }
    },
    handleGoToNote() {
      window.open('https://github.com/dzxrly/MHWIBDmgCalculator/blob/master/README.md')
    },
    handleSetElement() {
      if ((this.formData.weaponId === '12' || this.formData.weaponId === '13') && this.formData.weaponShowEl === '22') {
        this.formData.skillNumber = 0
        this.formData.action = 8
        this.formData.elementChanger = 1
      } else if ((this.formData.weaponId === '12' || this.formData.weaponId === '13') && this.formData.weaponShowEl === '18') {
        this.formData.skillNumber = 1
        this.formData.action = 2
        this.formData.elementChanger = 1
      } else {
        this.formData.skillNumber = ''
        this.formData.action = ''
        this.formData.elementChanger = ''
      }
    },
    isAwakeCheck() {
      let flag = 0
      this.formData.elSkillList.forEach((item) => {
        if (item.toString() === '9') flag = 1
        if (item.toString() === '10') flag = 2
      })
      return flag
    },
    isElCriticalCheck() {
      let flag = 0
      this.formData.elSkillList.forEach((item) => {
        if (item.toString() === '11') flag = 1
        if (item.toString() === '12') flag = 2
      })
      return flag
    },
    isBluntCheck(bladeNumber) {
      this.formData.otherAttSkillList.forEach((num) => {
        if (num.toString() === '37') return bladeNumber
      })
      return 0
    },
    dmgCalculator() {
      let baseDmg = baseLogic.getBaseDmg(this.formData.weaponId, this.formData.weaponShowAtt, this.formData.customAttLv, 0, this.formData.awakeAttackLv)
      let baseElDmg = baseLogic.getBaseElDmg(this.formData.weaponId, this.formData.weaponShowEl, this.formData.customElLv, 0, this.formData.awakeElementLv)
      let beforeDmgLimit = baseLogic.getBeforeDmgLimit(baseDmg, this.isBluntCheck(this.formData.bladeNumber), this.formData.baseAttSkillList, this.formData.otherAttSkillList)
      let beforeElLimit = baseLogic.getBeforeElLimit(baseElDmg, this.formData.elSkillList)
      let elDmgLimit = baseLogic.getElLimitation(this.formData.weaponId, baseElDmg, this.isAwakeCheck())
      let dmgLimit = baseLogic.getDmgLimitation(baseDmg)
      let afterDmgLimit = baseLogic.getAfterDmgLimit(beforeDmgLimit, dmgLimit)
      let afterElDmgLimit = baseLogic.getAfterElLimit(beforeElLimit, elDmgLimit)
      let sumAttack = baseLogic.getSumAttack(afterDmgLimit, this.formData.attLimitAfterRate)
      let sumElement = baseLogic.getSumElDmg(this.formData.weaponId, afterElDmgLimit, 1, this.isElCriticalCheck())
      let attRate = baseLogic.getAttRate(sumAttack)
      let physicDmgRate = baseLogic.getPhysicalDmgRate(
        this.formData.weaponId,
        this.formData.closeItemNumber,
        this.formData.farItemNumber,
        this.formData.bottleType,
        this.formData.angryRate,
        this.formData.isBladeCheck,
        this.formData.isScope
      )
      let physicDmg = baseLogic.getPhysicalDmg(this.formData.action, attRate, this.formData.criticalSituation, physicDmgRate)
      let elementDamege = baseLogic.getElementDmg(this.formData.weaponId, sumElement, attRate, this.formData.elementChanger)
      this.res.elDmg = elementDamege
      let basePHYMeatRate = baseLogic.getBasePHYMeatRate(this.formData.meatRate, this.formData.bladeNumber)
      let baseElMeatRate = baseLogic.getBaseElMeatRate(this.formData.elMeatRate, this.formData.bladeNumber)
      let afterClawMeat = baseLogic.getBaseMeatAfterClaw(this.formData.meatRate, 0, this.formData.bladeNumber)
      let afterClawMeatSp1 = baseLogic.getBaseMeatAfterClaw(this.formData.meatRate, 5, this.formData.bladeNumber)
      let afterClawMeatSp2 = baseLogic.getBaseMeatAfterClaw(this.formData.meatRate, -5, this.formData.bladeNumber)
      let toMonsterPHYDmg = baseLogic.getToMonsterPHYDmg(physicDmg, basePHYMeatRate, 0)
      let toMonsterElDmg = baseLogic.getToMonsterElDmg(elementDamege, baseElMeatRate)
      let monsterPhyDmgClaw = baseLogic.getToMonsterPHYDmg(physicDmg, afterClawMeat, 0)
      let monsterPhyDmgClawSp1 = baseLogic.getToMonsterPHYDmg(physicDmg, afterClawMeatSp1, 0)
      let monsterPhyDmgClawSp2 = baseLogic.getToMonsterPHYDmg(physicDmg, afterClawMeatSp2, 0)
      let totalDamge = baseLogic.getTotalDmg(toMonsterPHYDmg, toMonsterElDmg)
      this.res.toMonsterPHYDmg = toMonsterPHYDmg
      this.res.toMonsterElDmg = toMonsterElDmg
      this.res.totalDmg = totalDamge
      this.res.monsterPhyDmgClaw = monsterPhyDmgClaw + toMonsterElDmg
      this.res.monsterPhyDmgClawSp1 = monsterPhyDmgClawSp1 + toMonsterElDmg
      this.res.monsterPhyDmgClawSp2 = monsterPhyDmgClawSp2 + toMonsterElDmg
    },
    handleCalculator(formName) {
      this.dmgCalculator()
      // TODO
      // this.$refs[formName].validate(valid => {

      // })
    }
  }
}
</script>

<style lang="stylus" scoped>
/deep/ .el-input__inner {
  border-radius 0px
  border solid 1px #DCDFE6
  border-width 0 0 1px 0
}
/deep/ .el-input-number__decrease {
  border-radius 0px
}
/deep/ .el-input-number__increase {
  border-radius 0px
}
/deep/ .el-button--small {
  border-radius 0px
  background-color black
  border none
  box-shadow 0 3.2px 7.2px 0 rgba(0, 0, 0, 0.132), 0 0.6px 1.8px 0 rgba(0, 0, 0, 0.108)
}
/deep/ .el-button--small:hover {
  background-color rgba(0, 0, 0, 0.8)
}
.calculator-wrap {
  width 100%
  height 100%
  display flex
  flex-flow column nowrap
  position relative
  .calculator-pane {
    flex 1
    display flex
    flex-flow row wrap
    justify-content space-around
    align-items flex-start
    .input-area {
      flex 1 1 auto
      margin 20px
      .el-form {
        .el-form-item {
          .item-row {
            padding 5px 20px
            display flex
            flex-flow column nowrap
            justify-content center
            align-items flex-start
            box-shadow 0 3.2px 7.2px 0 rgba(0, 0, 0, 0.132), 0 0.6px 1.8px 0 rgba(0, 0, 0, 0.108)
            transition all 0.3s linear
            .row {
              display flex
              flex-flow row wrap
              justify-content flex-start
              .el-radio-group {
                margin 20px 5px
              }
            }
          }
          .awake-area {
            padding 5px 20px
            display flex
            flex-flow column nowrap
            justify-content flex-start
            align-items flex-start
            box-shadow 0 3.2px 7.2px 0 rgba(0, 0, 0, 0.132), 0 0.6px 1.8px 0 rgba(0, 0, 0, 0.108)
            .row {
              display flex
              flex-flow row wrap
              justify-content flex-start
            }
          }
          .base-skills-row {
            padding 5px 20px
            display flex
            flex-flow column wrap
            justify-content flex-start
            align-items flex-start
            box-shadow 0 3.2px 7.2px 0 rgba(0, 0, 0, 0.132), 0 0.6px 1.8px 0 rgba(0, 0, 0, 0.108)
            .el-checkbox-group {
              overflow-x auto
              display flex
              flex-flow column wrap
              justify-content flex-start
              align-items flex-start
              width 40vh
            }
          }
        }
      }
    }
    .result-area {
      position sticky
      position -webkit-sticky
      top 50px
      max-width 900px
      margin 20px
      flex 1 1 auto
      padding 5px 20px
      display flex
      flex-flow column nowrap
      justify-content flex-start
      align-items flex-start
      box-shadow 0 6.4px 14.4px 0 rgba(0, 0, 0, 0.132), 0 1.2px 3.6px 0 rgba(0, 0, 0, 0.108)
      .btn-row {
        margin 10px 0px
        display flex
        justify-content flex-end
        align-items center
        .note {
          margin 0px 10px
          line-height 35px
          cursor pointer
          span {
            color #909399
            font-size 12px
            font-weight bolder
          }
        }
        .note:hover {
          text-decoration underline #909399
        }
      }
      .row {
        margin 10px 0px
        display flex
        flex-flow row wrap
        justify-content flex-start
        align-items center
        span {
          font-size 14px
        }
        .pre {
          color #666666
        }
        .data {
          margin 0 10px
        }
      }
      .col {
        margin 10px 0px
        display flex
        flex-flow column wrap
        justify-content flex-start
        align-items flex-start
        span {
          font-size 14px
        }
        .pre {
          color #666666
        }
        .data {
          margin-top 5px
        }
      }
    }
    @media screen and (max-width 1920px) {
      .result-area {
        max-width 660px
      }
    }
    @media screen and (max-width 1200px) {
      .result-area {
        max-width 456px
      }
    }
    @media screen and (max-width 992px) {
      .result-area {
        max-width 344px
      }
    }
    @media screen and (max-width 768px) {
      .result-area {
        max-width 320px
      }
    }
  }
}
</style>



